html{
    scroll-behavior: smooth;
}
@media screen and (max-width: 999px)
{
    #headerDiv{
        
        background: #FFFBEC 70%;
        width: 100%;
        height: 8vw;
        padding: 2% 0;
        position: relative;
        overflow: hidden;
    
        #headerName{
            
            
            // display: inline;
            //display: inline-block;
            position: absolute;
            left: 5%;
            //top: 2%;
            font-family: Microsoft YaHei UI;
            font-size: x-large;
            font-size: 6vw;
            transition-duration: 1000ms;
        }

        #menu{
            position: absolute;
            //left: 4%;
            top: 30%;
            //left: 100%;
            //transform: translate(100%);
            //transform: translate(-80%);
            transition-duration: 1000ms;
        }


        #hamburgerMenu{
            position: absolute;
            right: 0%;
            
            padding: 0 5% 0 1%;
            background: #fff9e4;
            border: none;
            font-family: Arial, Helvetica, sans-serif;
            font-size: x-large;
            font-size: 6vw;
            margin: auto auto;

            // align-self: center;
            // justify-self: center;
            // width: fit-content;
            // height: fit-content;
            // 
            // grid-column: 3;
            // 
            
            // 
            // font-size: 6.5vw;
            // padding: 10% 10%;
        }
    }

    #menuAbout{
        text-decoration: none;
        color: black;
        padding: 0 3%;
        font-size: large;
        font-size: 5vw;
        font-family: Microsoft YaHei UI;
    }
    #menuSkills{
        text-decoration: none;
        color: black;
        padding: 0 3%;
        font-size: large;
        font-size: 5vw;
        font-family: Microsoft YaHei UI;
    }
    #menuProjects{

        display: none;

        text-decoration: none;
        color: black;
        padding: 0 3%;
        font-size: large;
        font-size: 5vw;
        font-family: Microsoft YaHei UI;
    }
    #menuContact{
        text-decoration: none;
        color: black;
        padding: 0 3%;
        font-size: large;
        font-size: 5vw;
        font-family: Microsoft YaHei UI;
    }
}

@media screen and (min-width: 1000px)
{
    #headerDiv{
    
        background: #FFFBEC 70%;
        width: 100%;
        height: 4.5vw;
        padding: 0;
        margin: 0;
        position: relative;

        #menu{
            display: grid;
            position: absolute;
            grid-template: 1fr/ auto auto auto auto;
            gap: 13%;
            color: #fff9e4;
            position: absolute;
            top: 20%;
            right: 12%;
        }
    
    
        #hamburgerMenu{
            display: none;
        }
    }
    
    #menuAbout{
        text-decoration: none;
        color: #EF626C;
        padding: 3% 5%;
        font-size: large;
        font-size: 2.2vw;
        font-family: Microsoft YaHei UI;
    }
    #menuSkills{
        text-decoration: none;
        color: #EF626C;
        padding: 3% 5%;
        font-size: large;
        font-size: 2.2vw;
        font-family: Microsoft YaHei UI;
    }
    #menuProjects{
        display: none;

        text-decoration: none;
        color: #EF626C;
        padding: 3% 5%;
        font-size: large;
        font-size: 2.2vw;
        font-family: Microsoft YaHei UI;
    }
    #menuContact{
        text-decoration: none;
        color: #EF626C;
        padding: 3% 5%;
        font-size: large;
        font-size: 2.2vw;
        font-family: Microsoft YaHei UI;
    }    

}